import React, { useEffect, useState } from 'react';
import axios from './axios';
import request from './request.jsx';
import './banner.css';

const Banner = () => {
    const [movie, setMovie] = useState([]);
    useEffect(() => {
        async function fetchData() {
            const req = await axios.get(request.fetchOriginal);
            setMovie(
                req.data.results[
                Math.floor(Math.random() * req.data.results.length - 1)
                ]
            );
            
        }
        fetchData();
    }, []);
    console.log(movie);
    return (
        <>
            <header className="banner" style = {{backgroundSize:"cover",backgroundImage :`url("https://image.tmdb.org/t/p/original/${movie?.backdrop_path}")`,backgroundPosition :"center center"}}>
                
                <div className="banner_content">
                    <h1 className ="banner_title">
                        {movie?.title || movie?.name || movie?.original_name}
                    </h1>
                    <div className="banner_buttons">
                        <button className="banner_button">
                            Play
                        </button>
                        <button className="banner_button">
                            My List
                        </button>
                        <h1 className="banner_disc">
                            { movie?.overview}
                        </h1>
                    </div>
    
                </div>
                <div className="banner_fadeBottom">
                    
                </div>
            </header>
        </>
    );
}

export default Banner;